:root {
  --main-bg: #282c34;
  --top-bg: Grey;
  --bottom-bg: darkGrey;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: var(--main-bg);
  min-height: 100vh;
  display: grid;
  place-content: center;
}

.card {
  display: grid;
  grid-template-columns: 2rem auto 2rem;
  grid-template-rows: repeat(6, auto);
  width: 45vw;
  text-align: center;
}

.card__name {
  background: var(--bottom-bg);
  border-radius: .5em;
  box-shadow: -6px 6px 11px 0px rgba(0,0,0,0.75);
  grid-column: 1 / -1;
  grid-row: 1 / 4;
  padding: 10px;

  h1 {
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4em;
    color: #fff;
  }
}

.card__skills {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--top-bg);
  border-radius: .3em;
  box-shadow: -5px 5px 5px -5px rgba(0,0,0,0.95);
  grid-column: 2 / 3;
  grid-row: 3 / 6;
  padding: 1em;
}

.card__skills-list {
  list-style-type: none;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin: 30px 0 0 0;
  padding: 0;
}

.card__skills-list-item {
  width: 100%;
  a {
    color: var(--main-bg);
  }
}